.close-icon {

    position: relative;
    width: 100%;
    height: 100%;
    display: block;

    transition: all 0.2s;

    transform-origin: 75% 50%;

    &::before,
    &::after {
        position: absolute;
        top: 1rem;
        left: 0;
        display: inline-block;
        width: 150%;
        height: 2px;
        background-color: white;

        content: "";

        
    }  

    &::before {
        transform: rotate(-135deg);
    }

    &::after {
        transform: rotate(135deg);
    }

    &:hover {
        transform: scale(1.2) rotate(90deg);
    }
   
}
